/* =========================
   üåç Global Reset & Base
========================= */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  min-height: 100vh;
  font-family: "Inter", system-ui, -apple-system, BlinkMacSystemFont, sans-serif;
  background: linear-gradient(135deg, #0f2027, #203a43, #2c5364);
  color: #f8fafc;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* =========================
   üì¶ App Container
========================= */
.app {
  width: 100%;
  max-width: 1000px;
  padding: 32px;
  text-align: center;
}

/* =========================
   üå§ Title
========================= */
.title {
  font-size: 2.8rem;
  font-weight: 800;
  margin-bottom: 30px;
  letter-spacing: 1px;
}

/* =========================
   üîç Search Bar
========================= */
.search {
  display: flex;
  justify-content: center;
  gap: 12px;
  margin-bottom: 35px;
}

.search input {
  width: 260px;
  padding: 14px 16px;
  border-radius: 14px;
  border: none;
  outline: none;
  font-size: 1rem;
}

.search button {
  padding: 14px 26px;
  border-radius: 14px;
  border: none;
  cursor: pointer;
  font-weight: 600;
  background: #38bdf8;
  color: #020617;
  transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.search button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 20px rgba(56, 189, 248, 0.45);
}

/* =========================
   üåç Current Weather Card
========================= */
.current-weather {
  margin: 30px auto;
  padding: 30px;
  max-width: 520px;
  border-radius: 22px;
  background: linear-gradient(135deg, #38bdf8, #0ea5e9);
  color: #020617;
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.35);
}

/* City Name */
.city-name {
  font-size: 2rem;
  margin-bottom: 20px;
}

/* Main Weather Section */
.weather-main {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
}

/* Temperature */
.temp {
  font-size: 3rem;
  font-weight: 700;
}

/* Weather Details */
.details p {
  font-size: 1.1rem;
  margin: 6px 0;
}

/* Min / Max Section */
.min-max {
  margin-top: 20px;
  padding-top: 15px;
  border-top: 1px solid rgba(0, 0, 0, 0.25);
  display: flex;
  justify-content: space-around;
  font-size: 1.1rem;
  font-weight: 600;
}

.min {
  color: #1e3a8a;
}

.max {
  color: #7c2d12;
}

/* =========================
   üìÖ Forecast Section
========================= */
.forecast {
  margin-top: 45px;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(170px, 1fr));
  gap: 22px;
}

/* Individual Forecast Card */
.forecast-card {
  padding: 22px;
  border-radius: 20px;
  text-align: center;
  color: #020617;
  font-weight: 600;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.28);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.forecast-card:hover {
  transform: translateY(-8px);
  box-shadow: 0 18px 45px rgba(0, 0, 0, 0.4);
}

.forecast-card h3 {
  font-size: 0.95rem;
  margin-bottom: 10px;
}

.forecast-card .temp {
  font-size: 1.7rem;
  margin: 10px 0;
}

.forecast-card .condition {
  font-size: 1rem;
  opacity: 0.85;
}

/* =========================
   üé® Forecast Day Colors
========================= */
.day-0 {
  background: linear-gradient(135deg, #38bdf8, #0ea5e9);
}

.day-1 {
  background: linear-gradient(135deg, #a78bfa, #7c3aed);
}

.day-2 {
  background: linear-gradient(135deg, #fbbf24, #f59e0b);
}

.day-3 {
  background: linear-gradient(135deg, #4ade80, #22c55e);
}

.day-4 {
  background: linear-gradient(135deg, #fb7185, #e11d48);
}

/* =========================
   üì± Responsive Design
========================= */
@media (max-width: 650px) {
  .title {
    font-size: 2.2rem;
  }

  .search {
    flex-direction: column;
  }

  .search input {
    width: 100%;
  }

  .weather-main {
    flex-direction: column;
    text-align: center;
  }

  .temp {
    font-size: 2.5rem;
  }
}
